import common from "./common";

import ItemPopovers from './parts/ItemPopovers.vue'
import ItemPopoversJs from "./parts/ItemPopovers.js";

export default {
  name: 'About',
  components: {
    Navigation,
    DamageSimulator,
    BattlePointSimulator,
    StatusSimulator,
    MessageBoard,
    ItemPopovers,
  },
  mixins: [common, ItemPopoversJs],

  data() {
    return {
      stat: {
        masterBonus: [
          { "": "", "1": [200, 0, 0, 0, 0, 0], "2": [400, 0, 0, 0, 0, 0], "3": [800, 0, 0, 0, 0, 0] },
